
'use server';
/**
 * @fileOverview A Genkit flow for submitting user feedback.
 * 
 * This file defines the backend logic for capturing user feedback and storing it in Firestore.
 * - submitFeedback: The main function that handles writing the feedback to the database.
 * - SubmitFeedbackInput: The Zod schema for the input data (name, email, feedback message).
 */

import { ai } from '@/ai/genkit';
import { z } from 'zod';
import { initializeApp, getApps, App, cert, ServiceAccount } from 'firebase-admin/app';
import { getFirestore, Firestore } from 'firebase-admin/firestore';

// Define the schema for the feedback form input.
const SubmitFeedbackInputSchema = z.object({
  name: z.string().optional().describe('The name of the user providing feedback.'),
  email: z.string().email({ message: "Invalid email address." }).optional().or(z.literal('')).describe('The email address of the user.'),
  rating: z.number().min(1, { message: "Please select a rating." }).max(5).describe('A rating from 1 to 5.'),
  feedback: z.string().min(10, { message: "Feedback must be at least 10 characters long." }).describe('The feedback message from the user.'),
});
export type SubmitFeedbackInput = z.infer<typeof SubmitFeedbackInputSchema>;

// Define the schema for the output.
const SubmitFeedbackOutputSchema = z.object({
    success: z.boolean(),
    message: z.string(),
});
export type SubmitFeedbackOutput = z.infer<typeof SubmitFeedbackOutputSchema>;


// Create an exported wrapper function that calls the flow.
export async function submitFeedback(input: SubmitFeedbackInput): Promise<SubmitFeedbackOutput> {
  return submitFeedbackFlow(input);
}

// Function to get an initialized Firestore instance, creating it if it doesn't exist.
// This makes the flow more resilient to initialization timing issues.
function getInitializedDb(): Firestore | null {
    const serviceAccountString = process.env.FIREBASE_SERVICE_ACCOUNT;
    if (!serviceAccountString) {
        console.error("CRITICAL: FIREBASE_SERVICE_ACCOUNT env var is not set.");
        return null;
    }

    let serviceAccount: ServiceAccount;
    try {
        serviceAccount = JSON.parse(serviceAccountString);
    } catch (e) {
        console.error("CRITICAL: Failed to parse FIREBASE_SERVICE_ACCOUNT JSON.", e);
        return null;
    }

    const adminAppName = 'submitFeedbackAdmin';
    let adminApp: App;

    if (!getApps().some(app => app.name === adminAppName)) {
        try {
            adminApp = initializeApp(
                {
                    credential: cert(serviceAccount),
                },
                adminAppName
            );
        } catch (error) {
            console.error("CRITICAL: A critical error occurred during Firebase Admin SDK initializeApp():", error);
            return null;
        }
    } else {
        adminApp = getApps().find(app => app.name === adminAppName)!;
    }
    
    return getFirestore(adminApp);
}


// Define the Genkit flow.
const submitFeedbackFlow = ai.defineFlow(
  {
    name: 'submitFeedbackFlow',
    inputSchema: SubmitFeedbackInputSchema,
    outputSchema: SubmitFeedbackOutputSchema,
  },
  async (input) => {
    const adminDb = getInitializedDb();

    // If Firestore isn't configured, we can't proceed.
    if (!adminDb) {
      console.error("Firestore is not available. Feedback cannot be saved.");
      return {
        success: false,
        message: "The server is not configured to save feedback. Please contact support.",
      };
    }

    try {
      // Add a new document with a generated ID to the 'feedback' collection.
      const feedbackRef = adminDb.collection('feedback').doc();
      await feedbackRef.set({
        ...input,
        createdAt: new Date().toISOString(), // Add a timestamp
      });

      console.log(`Feedback saved with ID: ${feedbackRef.id}`);

      return {
        success: true,
        message: "Thank you for your feedback!",
      };
    } catch (error) {
      console.error("Error writing feedback to Firestore:", error);
      return {
        success: false,
        message: "There was an error saving your feedback. Please try again later.",
      };
    }
  }
);
